.training__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 5rem 2.5rem 0 2.5rem;
    max-width: 1920px;
    width: 100%;
    margin: 0 auto;
}

.training__container-title {
    padding-bottom: .5rem;
    font-size: 3.5rem;
    font-weight: 400;
    line-height: 4rem;
    opacity: 0;
    transform: translateY(2rem);
    transition: all .3s ease-in-out;
    background: transparent;
}

.training__container-paragraph {
    font-size: 1.5rem;
    font-weight: 300;
    line-height: 1.5rem;
    color: rgba(254, 255, 179, .7);
    padding-bottom: 2.5rem;
    letter-spacing: 4px;
    opacity: 0;
    transform: translateY(2rem);
    transition: all .3s ease-in-out;
    background: transparent;
}

.training__container-content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 2rem;
    row-gap: 2.5rem;
    padding: 0 3rem 0 3rem;
}

.training__container-item {
    display: flex;
    width: 56rem;
    height: 33rem;
    border: 2px solid #FBE0F3;
    border-radius: 3px;
}

.training__container-item img {
    padding: 2.5rem;
}

.training__container-sub-title {
    font-size: 2.5rem;
    font-weight: 400;
    line-height: 2.5rem;
    color: #FEFFB3;
    padding: 2rem 0 1.5rem 0;
    text-align: start;
    margin-right: 1.5rem;
}

.training__container-item:nth-of-type(4) .training__container-sub-title {
    margin-right: 13rem;
}

.training__container-sub-paragraph {
    font-size: 1rem;
    font-weight: 200;
    line-height: 1rem;
    letter-spacing: .25rem;
    color: rgba(251, 224, 243, .7);
    text-align: start;
    padding-bottom: 1rem;
}

.training__container-buy-btn {
    border: 2px solid #FBE0F3;
    border-radius: 29px;
    display: flex;
    background: transparent;
    color: #FBE0F3;
    font-size: 1.5rem;
    font-weight: 400;
    line-height: 1.5rem;
    padding: 1rem 3.5rem 1rem 3.5rem;
    letter-spacing: 2.4px;
    transition: all .25s ease-in-out;
    white-space: nowrap;
}

.training__container-buy-btn:hover, .training__container-buy-btn:active {
    background: #E18186;
    color: black;
    border: 2px solid #E18186;
}

.training__container-buy-btn svg {
    width: 1.25rem;
    height: 1.25rem;
    padding: 0;
    margin-left: 1.5rem;
}

.training__container-subitem {
    padding-bottom: 2rem;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
}

.training__container-limited {
    font-size: 1.5rem;
    line-height: 1.5rem;
    font-weight: 300;
    letter-spacing: 4px;
    color: rgba(254, 255, 179, .7);
    padding: 2.5rem 0 5rem 0;
}

.visible {
    opacity: 1;
    transform: translateY(0);
}

.training__container-buy-container {
    display: flex;
    flex-direction: column;
    margin-top: auto;
    align-items: start;
    gap: 2rem;
}

.training__container-price {
    font-size: 1.5rem;
    line-height: 1.5rem;
    font-weight: 400;
    color: #FEFFB3;
}

/* Временно */
.alert {
  position: fixed;
  top: 2rem;
  right: calc(50% + -187px);
  background: url(/public/images/Frame.png), #222023;
  color: #E18186;
  font-weight: 600;
  font-size: 2rem;
  line-height: 2rem;
  padding: 1rem 2rem;
  border-radius: 5px;
  border: 2px solid #E18186;
  opacity: 0;
  transform: translateY(-10rem);
  transition: transform 0.25s ease, opacity 0.25s ease;
  z-index: 1000;
}

.alert-active {
  opacity: 1;
  transform: translateY(0);
  animation: shake 0.3s ease-in-out 0.3s;
}

@keyframes shake {
  0%, 100% { transform: translateY(0); }
  20%, 60% { transform: translateY(-0.5rem); }
  40%, 80% { transform: translateY(0.5rem); }
}
