.container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    max-width: 45.5rem;
    width: 100%;
    z-index: 10;
    border-radius: 3px;
    transform: translateY(25%);
}

.wrapper {
    left: 0;
    top: 0;
    position: absolute;
    width: 100%;
    height: 100vh;
    background: url(/public/images/Frame.png), #222023;
    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 5;
}

.wrapper_haeder {
    left: 0;
    top: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1905px;
    width: 100%;
    margin: 0 auto;
}

.wrapper_haeder h2 {
    font-size: 2rem;
    font-weight: 500;
    color: #FEFFB3;
    padding: 1rem 0 1.25rem 3rem;
    line-height: 2.375rem;
}

.paragraph {
    font-size: 2rem;
    line-height: 2rem;
    font-weight: 500;
    color: #FEFFB3;
    padding: 3rem 7.5rem 0 7.5rem;
}

.input {
    background: transparent;
    border: 2px solid #FBE0F3;
    outline: none;
    min-height: 4.625rem;
    height: 100%;
    max-width: 45.5rem;
    width: 100%;
    font-size: 1.5rem;
    color: #FBE0F3;
    padding: 0 1.5rem 0 1.5rem;
}

.input::placeholder {
    color: rgba(251, 224, 243, 0.5) ;
    font-size: 1rem;
    line-height: 1rem;
    font-weight: 400;
    text-align: start;
    border: 0;
    letter-spacing: 4px;
    padding: 1.875rem 0 1.875rem 0;
}

.utils {
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
}

.utils-element {
    cursor: pointer;
    color: #FBE0F3;
    text-decoration: underline;
    font-size: 1rem;
    font-weight: 300;
    line-height: 1rem;
    letter-spacing: 4px;
}

.btn {
    padding: 1rem 9.875rem 1rem 9.875rem;
    font-size: 1.5rem;
    font-weight: 200;
    line-height: 1.75rem;
    background: transparent;
    border: 2px solid #FBE0F3;
    color: #FBE0F3;
    cursor: pointer;
    letter-spacing: .25rem;
    display: flex;
    align-items: center;
    border-radius: 1.5rem;
    margin-top: 3rem;
    margin-bottom: 2.5rem;
}

.btn svg {
    margin-left: 1.5rem;
}

.agreement {
    text-decoration: underline;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1rem;
    letter-spacing: 1px;
    background: transparent;
    border: none;
    padding: 0;
    color: #FBE0F3;
    cursor: pointer;
}

.reset_btn {
    margin-top: 3rem;
}

.resetPassword-title {
    font-size: 2rem;
    font-weight: 500;
    line-height: 2.5rem;
    padding-left: 1rem;
    color: #FEFFB3;
}

.back_btn {
    background: transparent;
    width: 2rem;
    height: 2rem;
    padding: 0;
    border: 0;
    cursor: pointer;
}

.recovery_utils {
    display: flex;
    align-items: center;
    padding-bottom: 3rem;
    width: 100%;
}

.reset_back_btn

.wrapper_haeder button svg {
    width: 2rem;
    height: 2rem;
}

.wrapper_haeder button:hover {
    color:#E18186;
}

.wrapper_haeder button {
    color: black;
    background: none;
    border: 0;
    padding: 0;
    display: inline-block;
    padding-right: 3rem;
    cursor: pointer;
    height: 2rem;
    color: white;
    transition: all .15s ease-in-out;

}

.title {
    width: 100%;
    text-align: start;
    color: #FEFFB3;
    font-size: 1.5rem;
    font-weight: 300;
    letter-spacing: .25rem;
    padding-bottom: 2.5rem;
}

.buttons {
    display: flex;
    width: 100%;
    gap: 3.5rem;
    padding-bottom: 4rem;
    
}

.buttons button {
    background: transparent;
    border: none;
    padding: 0;
    color: rgba(254, 255, 179, 0.5);
    font-size: 2rem;
    font-weight: 400;
    letter-spacing: .125rem;
}

.checkbox {
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  width: 24px;
  height: 24px;
  border: 2px solid #FBE0F3;
  background-color: transparent;
  cursor: pointer;
  transition: all 0.2s ease;
  display: inline-block;
  vertical-align: middle;
  margin: 0 ;
  margin-right: 1rem;
}

.checkbox:active {
  transform: scale(0.95);
}

.checkbox:checked::before {
  content: "✔";
  display: flex;
  align-items: center;
  justify-content: center;
  color: #FBE0F3;
  font-size: 14px;
  font-weight: 600;
  height: 100%;
  width: 100%;
}   

.input_wrapper {
    position: relative;
    display: flex;
    align-items: center;
    width: 100%;
    justify-self: center;
    height: 100%;
}

.toggle {
    background: transparent;
    padding: 0;
    margin: 0;
    border: none;
    position: absolute;
    right: 0;
    cursor: pointer;
    width: 2rem;
    height: 2rem;
    margin-right: 1.5rem;
}

.input_container {
    display: flex;
    flex-direction: column;
    width: 100%;
    gap: 1.5rem;
    margin-bottom: 1rem;
}

.resetPassword-paragraph {
    color: #FEFFB3;
    font-weight: 300;
    font-size: 1.5rem;
    letter-spacing: .25rem;
    line-height: 1.5rem;
    width: 100%;
    margin-bottom: 3rem;
}

.newPassword-paragraph {
    color: #FEFFB3;
    font-weight: 300;
    font-size: 1.5rem;
    letter-spacing: .25rem;
    line-height: 1.5rem;
    width: 100%;
    text-align: start;
    margin-bottom: 3rem;
}

.newPasswod_input {
    background: transparent;
    border: 2px solid #FBE0F3;
    outline: none;
    min-height: 4.625rem;
    height: 100%;
    max-width: 45.5rem;
    width: 100%;
    font-size: 1.5rem;
    color: #FBE0F3;
    padding: 0 1.5rem 0 1.5rem;
}

.newPasswod_input:nth-last-of-type(1) {
    margin-top: 1.5rem;
}

.newPasswod_input::placeholder {
    color: rgba(251, 224, 243, 0.5) ;
    font-size: 1rem;
    line-height: 1rem;
    font-weight: 400;
    text-align: start;
    border: 0;
    letter-spacing: 4px;
    padding: 1.875rem 0 1.875rem 0;
}

.selected {
    text-decoration: underline;
    text-underline-offset: 5px;
    color: #FEFFB3 !important;
}

.title_register_password {
    width: 100%;
    text-align: start;
    color: #FEFFB3;
    font-size: 1.5rem;
    font-weight: 300;
    letter-spacing: .25rem;
    margin-bottom: .5rem;
}

.input_register_password {
    background: transparent;
    border: 2px solid #FBE0F3;
    outline: none;
    max-width: 44.5rem;
    width: 100%;
    font-size: 1.5rem;
    color: #FBE0F3;
    padding: 1.5rem 1.5rem 1.5rem 1.5rem;
}

.input_register_password::placeholder {
    color: rgba(251, 224, 243, 0.5) ;
    font-size: 1rem;
    line-height: 1rem;
    font-weight: 400;
    text-align: start;
    border: 0;
    letter-spacing: 4px;
}

.input_register_password:nth-of-type(1) {
    margin: 3rem 0 1.5rem 0;
}