.container {
    width: 27.5rem;
    height: 10rem;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    border: solid #FBE0F3 2px;
    border-radius: 3px;
    text-align: center;
    font-size: 1.25rem;
    font-weight: 400;
    line-height: 1.5rem;
    white-space: pre-wrap;
    letter-spacing: 2px;
}

.number {
    position: absolute;
    top: 0;
    left: 0;
    padding: .75rem;
    font-size: 1.5rem;
    font-weight: 400;
    line-height: 1.5rem;
    color: #FBE0F3;
}


.container:hover, .container:active {
    background: #E18186;
    border: 2px solid #E18186;
    color: #222023;
}

.container:hover p, .container:active p {
    color: #222023;
}

.text, .number, .container {
    transition: all .25s ease-in-out;
}

.container:hover .number, .container:active .number {
    color: #222023;
}

.text {
    background: transparent;
    font-size: 1.25rem;
    line-height: 1.25rem;
    letter-spacing: 1.5px;
}