:root {
    --main-text-color: #EDC9CB;
    --selected-text-color: #E18186;
}

html {
    font-family: 'Onest', sans-serif;
    font-weight: 400;
    font-size: 16px;
    color: #FBE0F3;
    scroll-behavior: smooth;
    overflow-x: hidden;
    background: url(../public/images/Frame.png), #222023;
    scroll-behavior: smooth;
    scrollbar-gutter: stable;
}

*, *::before, *::after {
    box-sizing: border-box;
    font-size: 16px;
}

body, h1, h2, h3, h4, h5, h6, p, ul {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

a {
    text-decoration: none;
    color: inherit;
}

li {
    list-style: none;
}

.container {
    padding: 0 3rem 0 3rem;
}

body.modalActive, html.modalActive {
    overflow: hidden;
}

input {
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  border: none;
  outline: none;
  background: transparent;
  box-shadow: none;
}