.description {
    white-space: pre-wrap;
}

.title {
    font-size: 1.5rem;
    font-weight: 300;
    line-height: 1.5rem;
    letter-spacing: .25rem;
    color: #FBE0F3;
    padding: 1.5rem 0 1.5rem 0;
    border-bottom: 2px solid #FBE0F3;
}

.container:first-child {
    border-top: 2px solid #FBE0F3;
}

.title button {
    background: transparent;
    border: none;
    padding: 0;
    margin: 0;
    cursor: pointer;
}

.title {
    display: flex;
    justify-content: space-between;
}

.icon {
    width: 2rem;
    height: 2rem;
    display: block;
    transition: transform 0.3s ease-in-out;
}

.description {
    color: #FEFFB3;
    font-size: 1.5rem;
    font-weight: 300;
    line-height: 2rem;
    letter-spacing: .25rem;
    padding: 0 0 0 2rem;
    max-height: 0;
    overflow: hidden;
    opacity: 0;
    transition: max-height 0.4s ease-out, padding 0.4s ease-out, opacity 0.4s ease-in-out;
}

.title.isopen .icon {
    transform: rotate(-45deg)
}

.title.isopen + .description {
    padding: 1.5rem 0 1.5rem 2rem;
    overflow: visible;
    opacity: 1;
}

.descriptionOpen {
    max-height: 5rem;
}

.large {
    max-height: 25rem;
}