
.header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 1809px;
    width: 100%;
    margin: 0 auto;
}

.header__title {
    color: #FEFFB3;
    font-weight: 500;
    font-size: 2rem;
    line-height: 2.375rem;
    padding: 1rem 0 1rem 0;
    width: 268px;
}

.header__nav-list {
    display: flex;
    gap: 2.1875rem;
}

.header__nav-link {
    font-size: 1rem;
    line-height: 1.5rem;
    font-weight: 500;
    transition: all .25s ease-in-out;
    color: #FBE0F3;
}

.header__nav-link:hover, .header__nav-link:active {
    color: #E18186;
}

.header__social {
    display: flex;
}

.header__utils {
    display: flex;
    box-sizing: border-box;
    width: 268px;
    justify-content: end;
}

.header__utils-user {
    cursor: pointer;
    height: 2rem;
    width: 2rem;
    transition: all .2s ease-in-out;
}

.header__utils-user svg {
    width: 100%;
    height: 100%;
}

.header__utils-user:hover {
    color: rgba(251, 224, 243, .7);
}

.header__utils-cart {
    width: 2rem;
    height: 2rem;
    transition: all .2s ease-in-out;
    cursor: pointer;
    color: #FBE0F3;
    margin-right: 1.5rem;
}

.header__utils-cart svg {
    width: 100%;
    height: 100%;
}

.header__utils-cart:hover {
    color: rgba(251, 224, 243, .7);
}