.purpose__container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
    opacity: 0;
    transform: translateY(2rem);
    transition: all .3s ease-in-out;
    background: transparent;
}

.purpose__container-p1 {
    padding-top: 10rem;
    line-height: 1.5rem;
    font-weight: 300;
    font-size: 1.5rem;
    color: rgba(251,224,243, .7);
    letter-spacing: .25rem;
}

.purpose__container-h {
    font-size: 3.5rem;
    font-weight: 400;
    line-height: 4rem;
    padding: 2.5rem 0 2.5rem 0;
}

.purpose__container-p2 {
    font-size: 1.25rem;
    font-weight: 300;
    line-height: 1.25rem;
    color: rgba(251,224,243, .7);
    letter-spacing: .25rem;
}

span {
    color: #FEFFB3;
    font-size: 3.5rem;
    font-weight: 400;
    line-height: 4rem;
}

.visible {
    opacity: 1;
    transform: translateY(0);
}