.body__container {
    display: flex;
    flex-direction: row;
    width: 1920px;
    margin: 0 auto;
}

.body_container-img {
    max-height: calc(100vh - 70px);
}

.body__container-content {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    opacity: 0;
    transform: translateY(2rem);
    transition: all .5s ease-in-out;
    background: transparent;
}

.body__container-content {
    text-align: center;
}

.body__container-content h1 {
    font-size: 5rem;
    font-weight: 400;
    line-height: 5.5rem;
    padding-bottom: 2.5rem;

}

.body__container-content img {
    padding-bottom: 1.5rem;
}

.body__container-content p {
    font-size: 1.5rem;
    font-weight: 300;
    color: rgba(251,224,243, .7);
    line-height: 1.5rem;
    letter-spacing: 4px;
}

.visible {
    opacity: 1;
    transform: translateY(0);
}