.footer__container {
    display: flex;
    padding: 0 3rem 0 3rem;
    max-width: 1905px;
    width: 100%;
    margin: 0 auto;
}

.footer__container-title {
    font-size: 3.5rem;
    font-weight: 500;
    line-height: 4rem;
    color: #FEFFB3;
    padding: 5rem 0 5rem 0;
}

.footer__container-nav {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: .5rem;
    padding: 3rem 0 0 10rem;
}

.footer__container-navTitle {
    font-size: 1.5rem;
    font-weight: 500;
    line-height: 1.5rem;
    letter-spacing: .25rem;
    color: #FEFFB3;
    padding-bottom: .5rem;
}

.footer__container-navBtn {
    color: #FBE0F3;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5rem;
    letter-spacing: .25rem;
    background:transparent;
    border: none;
    text-align: start;
    padding: 0;
    transition: all 0.15s ease-in-out;
    cursor: pointer;
}


.footer__container-contactsTitle {
    color: #FEFFB3;
    font-size: 1.5rem;
    font-weight: 500;
    line-height: 1.5rem;
    letter-spacing: .25rem;
    padding-bottom: 1rem;
}

.footer__container-contactsIcons {
    display: flex;
    gap: 2rem;
    height: 2rem;
    width: 2rem;
    border-radius: 50%;
}

.footer__container-contactsBtn {
    background: transparent;
    border: none;
    padding: 0;
    cursor: pointer;
    display: inline-block;
    border-radius: 50%;
}

.footer__container-contacts {
    display: flex;
    flex-direction: column;
    padding: 3rem 0 0 5rem;
}

.footer__container-policy {
    background: transparent;
    border: none;
    padding: 0;
    color: #FBE0F3;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5rem;
    margin-left: auto;
    margin-top: auto;
    padding-bottom: 3rem;
    height: fit-content;
    cursor: pointer;
    text-decoration: underline ;
}

.footer__container-navBtn:hover {
    color: #E18186;
}
