.diet__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding-top: 5rem;
    max-width: 1905px;
    width: 100%;
    margin: 0 auto;
}

.diet__container-content {
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding-bottom: 1.5rem;
}

.diet__container-title {
    color: #FEFFB3;
    font-size: 3.5rem;
    font-weight: 400;
    line-height: 4rem;
    letter-spacing: 4px;
    padding-bottom: 1rem;
}

.diet__container-paragraph {
    font-size: 1.5rem;
    font-weight: 300;
    line-height: 1.5rem;
    letter-spacing: 4px;
    color: rgba(251, 224, 243, .7);
    white-space: pre-wrap;
    padding-bottom: 3rem;
}

.swiper-slide img {
  display: block;
  width: 700px;
  height: 700px;
  object-fit: contain;
}

.swiper-slide {
    width: 700px;
    height: 700px;
}

.diet__container-text {
    flex-grow: 1;
    min-width: 0;
}

.diet__container-swiper {
  width: 700px;     /* фиксированная ширина */
  height: 700px;    /* фиксированная высота */
  margin: 0 auto;   /* центрирует слайдер */
  aspect-ratio: 1 / 1;/* форма квадрата сохраняется */
  overflow: visible;  /* не режем содержимое */
}

.diet__container-swiper :global(.swiper-button-next svg),
.diet__container-swiper :global(.swiper-button-prev svg) {
    display: none !important;
}   

.diet__container-swiper :global(.swiper-button-next), .diet__container-swiper :global(.swiper-button-prev) {
background-repeat: no-repeat;
background-position: center;
width: 80px;
height: 80px;
}

.diet__container-swiper :global(.swiper-button-prev) {
    background-image: url('/public/icons/left_btn.svg');
}

.diet__container-swiper :global(.swiper-button-next) {
    background-image: url('/public/icons/right_btn.svg');
}

.diet__container-detail {
    background: transparent;
    border: 2px solid #FBE0F3;
    border-radius: 25px;
    padding: 1rem;
    width: fit-content;
    font-size: 1.5rem;
    font-weight: 400;
    line-height: 1.5rem;
    letter-spacing: 2.4px;
    cursor: pointer;
    transition: all .3s ease-in-out;
}

.diet__container-subcontent {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-bottom: 5rem;
    position: relative;
}

.diet__container-detailModal {
    padding: 2.5rem 1.5rem 2.5rem 1.5rem;
    border: 2px solid #FBE0F3;
    border-radius: 3px;
    margin-top: 1rem;
    position: absolute;
    top: 65%;
    background: url(/public/images/Frame.png), #222023;
    opacity: 0;
    transition: opacity .2s ease-in-out;
    z-index: -1;
}

.isopen {
    opacity: 1;
    z-index: 1;
}


.diet__container-list {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    align-items: start;
}

.diet__container-list li img {
    width: 1.5rem;
    height: 1.5rem;
}

.diet__container-list li {
    white-space: pre-wrap;
    font-size: 1.5rem;
    font-weight: 300;
    line-height: 1.5rem;
    letter-spacing: .25rem;
    text-align: start;
    display: flex;
    align-items: center;
    gap: 1.5rem;
}

.diet__container-detail:hover, .diet__container-detail:active {
    background: #E18186;
    color: #222023;
    border: 2px solid #E18186;
}

.diet_slider_wrapper {
    display: flex;
    align-items: center;
    gap: 2rem;
    padding: 0 2rem 0 2rem;
}

.diet_slider_wrapper button {
    background: transparent;
    border: none;
    padding: 0;
    margin: 0;
    height: 2.5rem;
    height: 2.5rem;
    cursor: pointer;
    border-radius: 50%;
    border: 2px solid #262427;
}

.diet_slider_wrapper button svg {
    transition: filter 0.2s ease, opacity 0.2s ease;
    border-radius: 50%;
}

.diet_slider_wrapper button:disabled svg {
    filter: grayscale(1) brightness(0.7);
    opacity: 0.5;
    cursor: not-allowed;
}