.getrid__container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
    padding: 5rem 3rem 3rem 3rem;
    max-width: 1905px;
    width: 100%;
    margin: 0 auto;
}

.getrid__container-text {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}

.getrid__container-text h2 {
    font-size: 3.5rem;
    font-weight: 400;
    line-height: 4rem;
    color: #FBE0F3;
    text-align: start;
}

.getrid__container-text p {
    font-size: 1.5rem;
    font-weight: 300;
    line-height: 1.5rem;
    letter-spacing: 4px;
    color: rgba(254, 255, 179, .7);
    text-align: end;
}


.getrid__container-cards {
    display: flex;
    width: 100%;
    justify-content: space-between;
    padding-top: 1.25rem;
    opacity: 0;
    transform: translateY(2rem);
    transition: all .25s ease-in-out;
}

.getrid__container-cards > :nth-child(2), .getrid__container-cards > :nth-child(4) {
    margin-top: 5rem;
}

.visible {
    opacity: 1;
    transform: translateY(0);
}
