.diet__container-swiper {
    padding-bottom: 5rem;
}

.container__result-title {
    font-size: 3.5rem;
    font-weight: 400;
    line-height: 4rem;
    color: #FBE0F3;
    padding: 5rem 0 3rem 0;
}

.container__result-img {
    max-width: 43rem;
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.container__result {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding-bottom: 5rem;
}

.container__result-swiper {
    margin: 0 auto;
    overflow: hidden;
    max-width: 41.75rem;
    width: 100%;
    position: relative;
}

.container__result-img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    overflow: hidden;
    border-radius: 1px;
}


.container__result-swiper :global(.swiper-button-next svg),
.container__result-swiper :global(.swiper-button-prev svg) {
    display: none !important;
}   

.container__result-swiper :global(.swiper-button-next), .container__result-swiper :global(.swiper-button-prev) {
background-repeat: no-repeat;
background-position: center;
width: 80px;
height: 80px;
}

.container__result-swiper :global(.swiper-button-prev) {
    background-image: url('/public/icons/diet_left.svg');
}

.container__result-swiper :global(.swiper-button-next) {
    background-image: url('/public/icons/diet_right.svg');
}

.btn_left, .btn_right {
    background: transparent;
    border: 0;
    padding: 0;
    outline: none;
    position: absolute;
    z-index: 2;
    cursor: pointer;
    border-radius: 50%;
    width: 80px;
    height: 80px;
    border: 2px solid #262427;
}

.btn_left svg, .btn_right svg {
    border-radius: 50%;
    transition: filter 0.2s ease, opacity 0.2s ease;
}

.container__result button:disabled svg {
    filter: grayscale(1) brightness(0.7);
    opacity: 0.5;
    cursor: not-allowed;
}

.btn_left {
    margin-right: 55rem;
}

.btn_right {
    margin-left: 55rem;
}