.container__content {
    padding: 10rem 3rem 5rem 3rem;
    max-width: 1905px;
    width: 100%;
    margin: 0 auto;
}

.container__content-item1, .container__content-item3 {
    display: flex;
    gap: 1.5rem;
}

.container__content-item2 {
    display: grid;
    grid-template-columns: 1fr auto 1fr;
}

.container__content-item2 {
    padding: 1.5rem 0 1.5rem 0;
    justify-content: space-between;
}

h2 {
    font-size: 3.5rem;
    font-weight: 400;
    line-height: 4rem;
    text-align: center;
}

span {
    color: #FEFFB3;
}

.container__content-item1 > :last-child {
  margin-left: auto;
}

.container__content-item2 > :last-child {
    margin-left: 4rem;
}
.container__content-item2 > :nth-child(2) {
    align-self: center;
}

.container__content-item3 > :first-child {
    margin-left: 27.5rem;
}
.container__content-item3 > :last-child {
    margin-left: auto;
}